$('.feedbacks__slider-wrap').slick({
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    prevArrow: $('.feedbacks__prev'),
    nextArrow: $('.feedbacks__next'),
});

$(document).ready(function() {

    $('.works__more-btn').click(function () {
        $(this).toggleClass('works__more-btn--active')
        $('.works__cards').toggleClass('works__cards--active')
    })

})
